.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  height: 5vh;
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.2);
  background: aquamarine;
}
.productgrid {
  width: 80%;
  padding: 1rem;
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.2);
  background: #ffffff;
}
.filters {
  width: 20%;
  padding: 1rem;
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.2);
  background: #ffffff;
}
.productpage {
  display: flex;
  flex-direction: row;
  width: 100%;
  padding: 1rem;
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.2);
  background: #ffffff;
}
.card {
  padding: 1rem;
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.2);
  background: #ffffff;
  height: 100%;
  padding: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.cartitem {
  padding: 1rem;
  background: #ffffff;
  width: 400px;
  height: 200px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.cart {
  width: 80%;
  padding: 1rem;
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.2);
  background: #ffffff;
}

.cartimage {
  width: 10px;
  height: 10px;
}
.itemdetails {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}
.cartbuttons {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.total {
  display: flex;
  justify-content: space-between;
}
.title {
  right: 0px;
  padding: 50px;
}
Grid item {
  display: flex;
}
.searchbar {
  font-family: Lato;
  font-weight: 400;
  font-size: 1rem;
  line-height: 1.4375em;
  color: rgba(0, 0, 0, 0.87);
  box-sizing: border-box;
  cursor: text;
  display: inline-flex;
  -webkit-box-align: center;
  align-items: center;
  position: relative;
  border-radius: 4px;
  padding-right: 14px;
  -webkit-font-smoothing: antialiased;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding-top: 20px;
}
